.el-form-item__label,
.el-checkbox__label,
.el-radio__label {
  font-weight: 400;
}

.el-checkbox__inner.is-checked {
  background-color: $brand-primary;
  border-color: $brand-primary;
}

.el-select .el-input__inner, .el-input__inner {
  &:focus {
    border-color: $brand-primary
  }
;
}

.el-date-editor.is-active > .el-date-editor__editor, .el-date-editor:hover > .el-date-editor__editor, .el-date-editor__editor:focus {
  border-color: $brand-primary;

}

.el-button--primary {
  color: #fff;
  background-color: $brand-primary;
  border-color: $brand-primary;
  &:hover,&:active,&.is-active,&:focus {
    background-color: $brand-primary-light;
    border-color: $brand-primary-light;
  }
}

.el-button:focus, .el-button:hover {
  border-color: $brand-primary;
}

.el-textarea__inner:focus {
  border-color: $brand-primary;
}

.el-radio__inner {
  &.is-checked {
    border-color: $brand-primary;
    background: $brand-primary;
  }
  &:not(.is-disabled):hover{
    border-color: $brand-primary;
  }
}

.el-switch__input:checked+.el-switch__core {
  border-color: $brand-primary;
  background-color: $brand-primary;
}

